
.veui-select-options{border:1px solid #e7e7e7;margin:3px 0;transition:transform .2s;-ms-transform-origin:50% 0;transform-origin:50% 0;box-shadow:0 1px 4px rgba(0,0,0,.2)
}
.veui-overlay-enter .veui-select-options,.veui-overlay-leave-to .veui-select-options{-ms-transform:scaleY(0);transform:scaleY(0)
}
.tether-element-attached-bottom.veui-overlay-enter-active .veui-select-options,.tether-element-attached-bottom.veui-overlay-leave-active .veui-select-options{-ms-transform-origin:50% 100%;transform-origin:50% 100%
}
.veui-select-options.veui-dropdown-overflow:after,.veui-select-options.veui-dropdown-overflow:before{content:"";height:20px;pointer-events:none;transition:opacity .2s
}
.veui-select-options.veui-dropdown-overflow:before{position:absolute;top:3px;right:1px;bottom:0;left:1px;background-image:linear-gradient(180deg,#fff,transparent);background-repeat:repeat-x;filter:progid:DXImageTransform.Microsoft.Gradient(startColorstr="#ffffffff",endColorstr="#00000000",GradientType=0)
}
.veui-select-options.veui-dropdown-overflow:after{position:absolute;right:1px;bottom:3px;left:1px;background-image:linear-gradient(180deg,transparent,#fff);background-repeat:repeat-x;filter:progid:DXImageTransform.Microsoft.Gradient(startColorstr="#00000000",endColorstr="#ffffffff",GradientType=0)
}
.veui-select-options.veui-dropdown-overflow-scroll-end:after,.veui-select-options.veui-dropdown-overflow-scroll-start:before{opacity:0
}
.veui-select .veui-select-button{width:100%;padding-right:10px;padding-left:10px;text-align:left
}
.veui-select-label{display:inline-block;max-width:calc(100% - 24px);width:100%;overflow:hidden;text-overflow:ellipsis;white-space:nowrap
}
.veui-select-icon{float:right;transition:transform .2s
}
.veui-select-expanded .veui-select-icon{-ms-transform:rotate(180deg);transform:rotate(180deg)
}
.veui-select-button[ui~=primary]:not(.veui-button-loading):disabled{background-color:#eee;border-color:transparent;color:#b8b8b8
}
.veui-select-expanded .veui-select-button:not([ui~=link]),.veui-select-expanded .veui-select-button:not([ui~=link]).focus-visible,.veui-select-expanded .veui-select-button:not([ui~=link]):hover{color:#333;background-color:#f7f7f7
}
.veui-select-expanded .veui-select-button:not([ui~=link]):active{background-color:#e7e7e7
}
.veui-select-expanded .veui-select-button[ui~=primary]:not([ui~=link]),.veui-select-expanded .veui-select-button[ui~=primary]:not([ui~=link]).focus-visible,.veui-select-expanded .veui-select-button[ui~=primary]:not([ui~=link]):hover{background-color:#3389e3;color:#fff
}
.veui-select-expanded .veui-select-button[ui~=primary]:not([ui~=link]):active{background-color:#2e7aca
}
.veui-select-expanded .veui-select-button[ui~=alt],.veui-select-expanded .veui-select-button[ui~=alt].focus-visible,.veui-select-expanded .veui-select-button[ui~=alt]:hover{background-color:#e7e7e7
}
.veui-select.veui-input-invalid .veui-button.veui-select-button,.veui-select.veui-input-invalid .veui-button.veui-select-button:focus,.veui-select.veui-input-invalid .veui-button.veui-select-button:hover{border-color:#ff5b5b
}
.veui-select{display:inline-block;min-width:110px
}
.veui-select-options{min-width:110px;max-height:280px;overflow-y:auto;background-color:#fff;outline:none
}


