
.veui-option{transition-property:border-color,background-color,color,opacity,box-shadow;transition-duration:.2s;height:36px;line-height:36px;overflow:hidden;text-overflow:ellipsis;white-space:nowrap;display:block;width:100%;background-color:#fff;border:none;padding:0 10px;color:#666;text-align:left;outline:none;cursor:pointer
}
.veui-option.focus-visible,.veui-option:hover{background-color:#f7f7f7
}
.veui-option:active{background-color:#e7e7e7
}
.veui-option-selected{color:#333;font-weight:500
}
.veui-option-disabled{background-color:#fff;color:#999;cursor:not-allowed
}
.veui-option-label{float:left;margin-right:10px
}
.veui-option .veui-option-checkmark{float:right;display:none
}
[ui~=checkmark] .veui-option-checkmark{display:block;margin-top:calc(18px - .5em)
}


