
.veui-pagination{display:inline-block;color:#666
}
.veui-pagination:after,.veui-pagination:before{display:table;content:""
}
.veui-pagination:after{clear:both
}
.veui-pagination .veui-link{color:inherit;text-decoration:none;cursor:pointer
}
.veui-pagination .veui-link:focus{outline:none
}
.veui-pagination-info{display:none;float:left;margin-right:6px;line-height:30px
}
.veui-pagination-size,.veui-pagination-total{display:inline-block
}
.veui-pagination-size{margin-right:10px;margin-left:20px
}
.veui-pagination-switch{float:left
}
.veui-pagination-switch .veui-active a,.veui-pagination-switch .veui-active span{color:#333
}
.veui-pagination .veui-active a,.veui-pagination .veui-active span{background-color:#eee
}
.veui-pagination .veui-select{min-width:auto;margin-top:-1px;margin-left:10px;vertical-align:top
}
.veui-pagination .veui-select .veui-button{padding-top:0;padding-bottom:0;height:30px;line-height:28px;white-space:nowrap;color:#333
}
.veui-pagination .veui-select .veui-button .veui-icon{margin-top:calc(14px - .5em)
}
.veui-pagination .veui-select .veui-select-label{max-width:none;width:auto;float:left;margin-right:5px
}
.veui-pagination-next,.veui-pagination-prev{transition-property:border-color,background-color,color,opacity,box-shadow;transition-duration:.2s;position:relative;float:left;width:30px;height:30px;line-height:28px;text-align:center;border:1px solid #dbdbdb;background:transparent
}
.veui-pagination-next:hover,.veui-pagination-prev:hover{color:#333;background-color:#f7f7f7
}
.veui-pagination-next.focus-visible,.veui-pagination-prev.focus-visible{box-shadow:0 0 0 2px hsla(0,0%,86%,.5);outline:none;color:#3998fc
}
.veui-pagination-next:active,.veui-pagination-prev:active{background-color:#e7e7e7
}
.veui-pagination-next.veui-disabled,.veui-pagination-next.veui-disabled:active,.veui-pagination-next.veui-disabled:hover,.veui-pagination-prev.veui-disabled,.veui-pagination-prev.veui-disabled:active,.veui-pagination-prev.veui-disabled:hover{background-color:#eee;border-color:transparent;color:#b8b8b8
}
.veui-pagination-next .veui-icon,.veui-pagination-prev .veui-icon{position:absolute;top:auto;right:50%;bottom:50%;left:auto;-ms-transform:translate(50%,50%);transform:translate(50%,50%)
}
.veui-pagination-prev{margin-right:6px
}
.veui-pagination-next{margin-left:6px
}
.veui-pagination-pages{float:left;min-height:1px;margin:0;padding:0;list-style:none
}
.veui-pagination-pages li{float:left;width:28px;height:30px;text-align:center;line-height:30px;font-size:14px
}
.veui-pagination-pages li .veui-link{position:relative;display:inline-block;min-width:30px;transition-property:border-color,background-color,color,opacity,box-shadow;transition-duration:.2s;padding-right:6px;padding-left:6px
}
.veui-pagination-pages li .veui-link:hover{color:#333
}
.veui-pagination-pages li .veui-link.focus-visible{color:#2e7aca;z-index:1;background-color:#fff;box-shadow:0 0 0 2px hsla(0,0%,72%,.5);outline:none
}
.veui-pagination-pages li .veui-current{cursor:default
}
.veui-pagination-pages span{color:#999
}
.veui-pagination .veui-pagination-digit-length-4 li{width:40px
}
.veui-pagination .veui-pagination-digit-length-3 li{width:34px
}
.veui-pagination[ui~=full] .veui-pagination-switch,.veui-pagination[ui~=hetero] .veui-pagination-switch{position:relative
}
.veui-pagination[ui~=full] .veui-pagination-pages,.veui-pagination[ui~=hetero] .veui-pagination-pages{margin-right:42px
}
.veui-pagination[ui~=full] .veui-pagination-prev,.veui-pagination[ui~=hetero] .veui-pagination-prev{position:absolute;right:30px
}
.veui-pagination[ui~=full] .veui-pagination-next,.veui-pagination[ui~=hetero] .veui-pagination-next{margin-left:0
}
.veui-pagination[ui~=full] .veui-pagination-info{display:block
}
.veui-pagination[ui~=slim] .veui-pagination-switch li{width:18.66666667px;height:20px;line-height:20px;font-size:12px
}
.veui-pagination[ui~=slim] .veui-pagination-switch li a{min-width:20px
}
.veui-pagination[ui~=slim] .veui-pagination-next,.veui-pagination[ui~=slim] .veui-pagination-prev{width:20px;height:20px;line-height:1.5
}
.veui-pagination[ui~=slim] .veui-icon{height:10px
}
.veui-pagination[ui~=slim] .veui-pagination-digit-length-4 li{width:32px
}
.veui-pagination[ui~=slim] .veui-pagination-digit-length-3 li{width:27.2px
}


