
.veui-input.veui-input-invalid,.veui-input.veui-input-invalid:focus,.veui-input.veui-input-invalid:hover{border-color:#ff5b5b
}
.veui-input{display:inline-table;position:relative;height:36px;line-height:34px;width:210px;border:1px solid #dbdbdb;background-color:#fff;color:#666;transition-property:border-color,color,box-shadow;transition-duration:.2s
}
.veui-input-autofill{background-color:#e8f0fe
}
.veui-field>.veui-input{display:inline-table
}
.veui-input-hidden{display:none!important
}
.veui-input-after,.veui-input-before,.veui-input-clear,.veui-input-main{display:table-cell;vertical-align:top;height:100%
}
.veui-input-main{width:100%;cursor:inherit
}
.veui-input-input{display:block;position:relative;overflow:hidden;width:100%;height:100%;border:none;outline:none;padding:0 10px;background-color:transparent;text-overflow:ellipsis
}
.veui-input-clear{padding-right:12px
}
.veui-input-clear-button{width:1em;height:1em;padding:0;vertical-align:calc(-50% + .5em);vertical-align:-3px\9\0;border:none;background-color:transparent;color:#666;outline:none;cursor:unset;transition-property:border-color,background-color,color,opacity,box-shadow;transition-duration:.2s
}
.veui-input-clear-button.focus-visible{color:#333;box-shadow:0 0 0 2px hsla(0,0%,72%,.5);outline:none
}
.veui-input-clear .veui-icon{cursor:pointer;vertical-align:top
}
.veui-input-clear .veui-icon:hover{color:#333
}
.veui-input-focused,.veui-input:hover{border-color:#999
}
.veui-input-focused{box-shadow:0 0 0 2px hsla(0,0%,72%,.5);outline:none;color:#333
}
.veui-input.veui-disabled,.veui-input.veui-readonly{background-color:#f7f7f7
}
.veui-input.veui-disabled,.veui-input.veui-disabled:hover,.veui-input.veui-readonly,.veui-input.veui-readonly:hover{border-color:#dbdbdb;color:#999
}
.veui-input.veui-disabled{cursor:not-allowed
}
.veui-input.veui-disabled .veui-input-input{pointer-events:none
}
.veui-input-placeholder{position:absolute;padding-left:10px;color:#999!important;pointer-events:none;cursor:text;overflow:hidden;text-overflow:ellipsis;white-space:nowrap;width:100%
}
.veui-input[ui~=large]{height:42px;line-height:40px;font-size:16px
}
.veui-input[ui~=small]{height:32px;line-height:30px;font-size:12px
}
.veui-input[ui~=tiny]{height:28px;line-height:26px;font-size:12px
}
.veui-input[ui~=micro]{height:22px;line-height:20px;font-size:12px
}


