
.veui-button{padding:0 20px;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;vertical-align:middle;transition-property:border-color,background-color,color,opacity,box-shadow;transition-duration:.2s;border:1px solid #dbdbdb;background-color:#fff;color:#666;height:36px;line-height:34px
}
.veui-button:active,.veui-button:hover{color:#333
}
.veui-button:hover{background-color:#f7f7f7
}
.veui-button:active{background-color:#e7e7e7
}
.veui-button:focus{outline:none
}
.veui-button.focus-visible{box-shadow:0 0 0 2px hsla(0,0%,86%,.5);outline:none;color:#3998fc
}
.veui-button[ui~=primary]{border-color:transparent;background-color:#3998fc;color:#fff
}
.veui-button[ui~=primary]:hover{background-color:#3389e3
}
.veui-button[ui~=primary].focus-visible{box-shadow:0 0 0 2px rgba(116,183,253,.5);outline:none
}
.veui-button[ui~=primary]:active{background-color:#2e7aca
}
.veui-button[ui~=alt]{border-color:transparent;background-color:#eee
}
.veui-button[ui~=alt]:hover{background-color:#e7e7e7
}
.veui-button[ui~=alt]:active{background-color:#dbdbdb
}
.veui-button[ui~=alt].focus-visible{box-shadow:0 0 0 2px hsla(0,0%,72%,.5);outline:none;color:#3998fc
}
.veui-button[ui~=dark]{border-color:transparent;background-color:rgba(0,0,0,.7);color:#fff
}
.veui-button[ui~=dark].focus-visible,.veui-button[ui~=dark]:active,.veui-button[ui~=dark]:hover{background-color:rgba(0,0,0,.8)
}
.veui-button[ui~=dark].focus-visible{box-shadow:0 0 0 2px rgba(0,0,0,.4);outline:none
}
.veui-button[ui~=large]{line-height:42px;font-size:16px;height:42px;line-height:40px
}
.veui-button[ui~=large] .veui-icon{vertical-align:top;margin-top:calc(20px - .5em)
}
.veui-button[ui~=small]{line-height:32px;font-size:12px;height:32px;line-height:30px;padding-right:10px;padding-left:10px
}
.veui-button[ui~=small] .veui-icon{vertical-align:top;margin-top:calc(15px - .5em)
}
.veui-button[ui~=tiny]{line-height:28px;font-size:12px;height:28px;line-height:26px;padding-right:10px;padding-left:10px
}
.veui-button[ui~=tiny] .veui-icon{vertical-align:top;margin-top:calc(13px - .5em)
}
.veui-button[ui~=micro]{min-width:auto;line-height:22px;height:22px;line-height:20px;font-size:12px;padding-right:10px;padding-left:10px
}
.veui-button[ui~=micro] .veui-icon{vertical-align:top;margin-top:calc(10px - .5em)
}
.veui-button[ui~=round]{border-radius:50%
}
.veui-button[ui~=round],.veui-button[ui~=square]{width:36px;min-width:auto;padding-right:0;padding-left:0;text-align:center
}
.veui-button[ui~=round][ui~=large],.veui-button[ui~=square][ui~=large]{width:42px
}
.veui-button[ui~=round][ui~=small],.veui-button[ui~=square][ui~=small]{width:32px
}
.veui-button[ui~=round][ui~=tiny],.veui-button[ui~=square][ui~=tiny]{width:28px
}
.veui-button[ui~=round][ui~=micro],.veui-button[ui~=square][ui~=micro]{width:22px
}
.veui-button.veui-disabled,.veui-button.veui-disabled:hover,.veui-button.veui-disabled[ui],.veui-button.veui-disabled[ui]:hover{cursor:not-allowed;border-color:transparent;background-color:#eee;color:#b8b8b8
}
.veui-button[ui~=link]{display:inline;min-width:auto;border:none;padding:0;outline-color:transparent;transition-property:color,outline-color;transition-duration:.2s
}
.veui-button[ui~=link],.veui-button[ui~=link].focus-visible,.veui-button[ui~=link]:active,.veui-button[ui~=link]:hover{background-color:transparent
}
.veui-button[ui~=link].focus-visible{outline:2px solid rgba(51,137,227,.5);outline-offset:2px;box-shadow:none
}
.veui-button[ui~=link].veui-disabled,.veui-button[ui~=link].veui-disabled:hover{background-color:transparent
}
.veui-button-loading,.veui-button-loading[ui]{cursor:default;pointer-events:none;opacity:.5
}
.veui-button .veui-icon{max-width:1em
}
.veui-button .veui-icon+.veui-button-loading-text{margin-left:5px
}
.veui-button .veui-icon{vertical-align:top;margin-top:calc(17px - .5em)
}
.veui-button-loading-text{vertical-align:top
}
[ui~=round]>.veui-button-loading-text,[ui~=square]>.veui-button-loading-text{display:none
}


