
.veui-progress{display:inline-block;position:relative;color:#999;transition-property:color;transition-duration:.2s
}
.veui-progress:after,.veui-progress:before{display:table;content:""
}
.veui-progress:after{clear:both
}
.veui-progress[ui~=fluid]{width:100%
}
.veui-progress-bar{height:36px;line-height:36px
}
.veui-progress-bar .veui-progress-rail{width:250px;height:4px;overflow:hidden;float:left;margin-top:16px;background-color:#dbdbdb
}
.veui-progress-bar[ui~=fluid] .veui-progress-rail{width:100%
}
.veui-progress-bar[ui~=fluid] .veui-progress-desc+.veui-progress-rail{width:calc(100% - 60px)
}
.veui-progress-bar .veui-progress-meter{width:100%;height:100%;margin-left:-100%;background-color:#666;transition-property:transform,background-color;transition-duration:.2s
}
.veui-progress-circular{height:auto
}
.veui-progress-circular .veui-progress-circle{display:block;-ms-transform:rotate(-90deg);transform:rotate(-90deg)
}
.veui-progress-circular .veui-progress-rail{stroke:#dbdbdb
}
.veui-progress-circular .veui-progress-meter{stroke:#666;transition-property:stroke-dashoffset;transition-duration:.2s
}
.veui-progress-desc{float:right;padding-left:10px;white-space:nowrap
}
.veui-progress[ui~=tiny] .veui-progress-desc-text{display:none
}
.veui-progress[ui~=tiny] .veui-icon{font-size:20px
}
.veui-progress-circular .veui-progress-desc{position:absolute;top:auto;right:50%;bottom:50%;left:auto;-ms-transform:translate(50%,50%);transform:translate(50%,50%);height:20px;line-height:20px;padding-left:0;font-size:16px
}
.veui-progress-circular .veui-progress-desc .veui-icon{vertical-align:top;margin-top:calc(10px - .5em)
}
.veui-progress-circular .veui-progress-desc .veui-icon+.veui-progress-desc-text{margin-left:5px
}
.veui-progress-status-complete{color:#666
}
.veui-progress-status-success{color:#5bc49f
}
.veui-progress-status-success .veui-progress-meter{background-color:#5bc49f;stroke:#5bc49f
}
.veui-progress-status-alert{color:#ff5b5b
}
.veui-progress-status-alert .veui-progress-meter{background-color:#ff5b5b;stroke:#ff5b5b
}


