
.veui-table{color:#666
}
.veui-table table{width:100%;table-layout:fixed;border-collapse:collapse
}
.veui-table-fixed-header{position:relative;margin-bottom:-1px;border-bottom:1px solid #eee
}
.veui-table-fixed-footer{margin-top:-1px;border-top:1px solid #eee
}
.veui-table-fixed-header+.veui-table-main{overflow:auto
}
.veui-table-cell{display:inline-block;line-height:2;vertical-align:middle
}
.veui-table-col-select{width:60px
}
.veui-table-col-expand{width:24px
}
.veui-table td,.veui-table th{height:54px;padding-right:16px;padding-left:16px;text-align:left;white-space:nowrap;transition-property:background-color;transition-duration:.2s
}
.veui-table th{font-weight:500
}
.veui-table td{border:1px solid #eee;border-style:solid none
}
.veui-table tfoot th{border-top:none;border-bottom:1px solid #eee
}
td.veui-table-no-data{height:300px;text-align:center
}
.veui-table tr:hover td{background-color:#f7f7f7
}
.veui-table tr:hover .veui-table-no-data{background-color:transparent
}
.veui-table .veui-button+.veui-button,.veui-table .veui-button+.veui-link,.veui-table .veui-link+.veui-button,.veui-table .veui-link+.veui-link{margin-left:30px
}
.veui-table-header{display:inline-block;vertical-align:middle
}
.veui-table .veui-table-column-center{text-align:center
}
.veui-table .veui-table-column-right{text-align:right;padding-right:38px
}
.veui-table .veui-sorter{margin-left:5px;font-size:12px
}
.veui-table .veui-table-cell-expand{padding:0 0 0 10px;text-align:center
}
.veui-table .veui-table-cell-expand+td,.veui-table .veui-table-cell-expand+th{padding-left:10px
}
.veui-table-cell-expand button{width:1em;height:1em;background:none;outline:none;border:none;padding:0;color:#999;cursor:pointer;vertical-align:middle;transition-property:border-color,background-color,color,opacity,box-shadow;transition-duration:.2s
}
.veui-table-cell-expand button.focus-visible,.veui-table-cell-expand button:hover{color:#3998fc
}
.veui-table-cell-expand button.focus-visible{box-shadow:0 0 0 2px hsla(0,0%,72%,.5);outline:none
}
.veui-table-cell-expand button:active{color:#2e7aca
}
.veui-table-cell-expand button .veui-icon{display:block
}
.veui-table-sub-row td{background-color:#f7f7f7
}
.veui-table-sub-row:hover .veui-table-cell-expand{background-color:#fff
}
.veui-table-sub-row .veui-table-cell-expand{position:relative;background-color:#fff
}
.veui-table-sub-row .veui-table-cell-expand:before{content:"";position:absolute;top:-1px;right:0;left:0;border-top:1px solid #fff
}
.veui-table[ui~=slim] td,.veui-table[ui~=slim] th{height:44px
}
.veui-table[ui~=alt] thead th{background-color:#f7f7f7
}
.veui-table[ui~=bordered] td{border-style:solid
}
.veui-table[ui~=bordered] td:first-child{border-left:none
}
.veui-table[ui~=bordered] td:last-child{border-right:none
}
.veui-table-expander{transition-property:transform;transition-duration:.2s
}
.veui-table-expander-collapse{-ms-transform:rotate(90deg);transform:rotate(90deg)
}


