
.veui-switch{display:inline-block;cursor:pointer;outline:none
}
.veui-switch .veui-switch-switcher{width:50px;height:24px;border-radius:12px
}
.veui-switch .veui-switch-button{width:20px;height:20px
}
.veui-switch .veui-switch-label{line-height:24px
}
.veui-switch :checked+.veui-switch-switcher .veui-switch-button{-ms-transform:translateX(26px);transform:translateX(26px)
}
.veui-switch-label,.veui-switch-switcher{vertical-align:middle
}
.veui-switch-label{display:inline-block;margin-left:10px
}
.veui-switch-switcher{display:inline-block;background-color:#b8b8b8;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;transition-property:background-color,box-shadow;transition-duration:.2s
}
.focus-visible+.veui-switch-switcher{box-shadow:0 0 0 2px hsla(0,0%,72%,.5);outline:none
}
:checked.focus-visible+.veui-switch-switcher{box-shadow:0 0 0 2px rgba(116,183,253,.5);outline:none
}
.veui-switch-button{display:inline-block;position:relative;margin:2px;background-color:#fff;border-radius:50%;transition-property:transform;transition-duration:.2s
}
:checked+.veui-switch-switcher{background-color:#3998fc
}
.veui-switch.veui-readonly{cursor:default
}
.veui-switch.veui-disabled{cursor:not-allowed
}
.veui-switch.veui-disabled .veui-switch-switcher,.veui-switch.veui-readonly .veui-switch-switcher{background-color:#dbdbdb
}
.veui-switch[ui~=small] .veui-switch-switcher{width:37.5px;height:18px;border-radius:9px
}
.veui-switch[ui~=small] .veui-switch-button{width:14px;height:14px
}
.veui-switch[ui~=small] .veui-switch-label{line-height:18px
}
.veui-switch[ui~=small] :checked+.veui-switch-switcher .veui-switch-button{-ms-transform:translateX(19.5px);transform:translateX(19.5px)
}
.veui-switch[ui~=large] .veui-switch-switcher{width:62.5px;height:30px;border-radius:15px
}
.veui-switch[ui~=large] .veui-switch-button{width:26px;height:26px
}
.veui-switch[ui~=large] .veui-switch-label{line-height:30px
}
.veui-switch[ui~=large] :checked+.veui-switch-switcher .veui-switch-button{-ms-transform:translateX(32.5px);transform:translateX(32.5px)
}
.veui-switch input{position:absolute;opacity:0
}


