
.veui-radio{display:inline-block;color:#666;font-size:14px;line-height:16px;cursor:pointer
}
.veui-radio input{position:absolute;opacity:0
}
.veui-radio .veui-icon{display:none
}
.veui-radio-box,.veui-radio-label{vertical-align:top;display:inline-block;transition-property:color;transition-duration:.2s
}
.veui-field>.veui-radio{line-height:36px
}
.veui-field>.veui-radio .veui-radio-box{vertical-align:text-top
}
.veui-radio .focus-visible~.veui-radio-box,.veui-radio .focus-visible~.veui-radio-label,.veui-radio:hover{color:#333
}
.veui-radio .focus-visible~.veui-radio-box{box-shadow:0 0 0 2px hsla(0,0%,72%,.5);outline:none
}
.veui-radio-box{position:relative;border:1px solid #666;background-color:#fff;margin-right:8px;border-radius:50%;font-size:14px;transition-property:box-shadow
}
.veui-radio-box,.veui-radio-box:before{height:16px;width:16px;transition-duration:.2s
}
.veui-radio-box:before{content:"";display:block;background-color:#333;border-radius:50%;position:absolute;top:50%;right:50%;bottom:50%;left:50%;-ms-transform:translate(-50%,-50%) scale(0);transform:translate(-50%,-50%) scale(0);transition-property:transform
}
.veui-radio :checked+.veui-radio-box,.veui-radio:hover .veui-radio-box{border-color:#333
}
.veui-radio :checked+.veui-radio-box:before{-ms-transform:translate(-50%,-50%) scale(.45);transform:translate(-50%,-50%) scale(.45)
}
.veui-radio.veui-disabled{cursor:not-allowed
}
.veui-radio.veui-disabled .veui-radio-label{color:#999
}
.veui-radio :disabled+.veui-radio-box{border-color:#dbdbdb;background-color:#eee
}
.veui-radio :disabled+.veui-radio-box:before{background-color:#dbdbdb
}
.veui-radio[ui~=small]{font-size:12px;line-height:12px
}
.veui-field[ui~=small]>.veui-radio[ui~=small]{line-height:32px
}
.veui-field[ui~=small]>.veui-radio[ui~=small] .veui-radio-box{vertical-align:-2px
}
.veui-radio[ui~=small] .veui-radio-box{margin-right:6px;font-size:12px;height:12px;width:12px
}
.veui-radio[ui~=small] .veui-radio-box:before{width:12px;height:12px
}


