
.veui-searchbox-suggestion-overlay{border:1px solid #e7e7e7;margin:3px 0;outline:none;transition:transform .2s;-ms-transform-origin:50% 0;transform-origin:50% 0;box-shadow:0 1px 4px rgba(0,0,0,.2)
}
.veui-overlay-enter .veui-searchbox-suggestion-overlay,.veui-overlay-leave-to .veui-searchbox-suggestion-overlay{-ms-transform:scaleY(0);transform:scaleY(0)
}
.tether-element-attached-bottom.veui-overlay-enter-active .veui-searchbox-suggestion-overlay,.tether-element-attached-bottom.veui-overlay-leave-active .veui-searchbox-suggestion-overlay{-ms-transform-origin:50% 100%;transform-origin:50% 100%
}
.veui-searchbox-suggestion-overlay.veui-dropdown-overflow:after,.veui-searchbox-suggestion-overlay.veui-dropdown-overflow:before{content:"";height:20px;pointer-events:none;transition:opacity .2s
}
.veui-searchbox-suggestion-overlay.veui-dropdown-overflow:before{position:absolute;top:3px;right:1px;bottom:0;left:1px;background-image:linear-gradient(180deg,#fff,transparent);background-repeat:repeat-x;filter:progid:DXImageTransform.Microsoft.Gradient(startColorstr="#ffffffff",endColorstr="#00000000",GradientType=0)
}
.veui-searchbox-suggestion-overlay.veui-dropdown-overflow:after{position:absolute;right:1px;bottom:3px;left:1px;background-image:linear-gradient(180deg,transparent,#fff);background-repeat:repeat-x;filter:progid:DXImageTransform.Microsoft.Gradient(startColorstr="#00000000",endColorstr="#ffffffff",GradientType=0)
}
.veui-searchbox-suggestion-overlay.veui-dropdown-overflow-scroll-end:after,.veui-searchbox-suggestion-overlay.veui-dropdown-overflow-scroll-start:before{opacity:0
}
.veui-searchbox-suggestion-item{transition-property:border-color,background-color,color,opacity,box-shadow;transition-duration:.2s;height:36px;line-height:36px;overflow:hidden;text-overflow:ellipsis;white-space:nowrap;display:block;width:100%;background-color:#fff;border:none;padding:0 10px;color:#666;text-align:left;outline:none;cursor:pointer
}
.veui-searchbox-suggestion-item.focus-visible,.veui-searchbox-suggestion-item:hover{background-color:#f7f7f7
}
.veui-searchbox-suggestion-item:active{background-color:#e7e7e7
}
.veui-searchbox-suggestion-item-selected{color:#333;font-weight:500
}
.veui-searchbox-suggestion-item-disabled{background-color:#fff;color:#999;cursor:not-allowed
}
.veui-searchbox-suggestion-item-label{float:left;margin-right:10px
}
.veui-searchbox{display:inline-block;position:relative;color:#999;width:210px;background-color:#fff
}
.veui-searchbox .veui-input{width:100%;vertical-align:top;overflow:visible
}
.veui-searchbox-action{color:#666;white-space:nowrap;margin:-1px -1px -1px 0
}
.veui-searchbox-action-icon{outline:none;border:none;margin-top:calc(18px - .5em);margin-right:10px;padding:0;width:auto;height:auto;line-height:1;vertical-align:top;background-color:transparent;transition-property:border-color,background-color,color,opacity,box-shadow;transition-duration:.2s
}
[ui~=large] .veui-searchbox-action-icon{margin-top:calc(21px - .5em)
}
[ui~=small] .veui-searchbox-action-icon{margin-top:calc(16px - .5em)
}
.veui-searchbox-action-icon .veui-icon{vertical-align:top
}
.veui-searchbox-action-icon.focus-visible{box-shadow:0 0 0 2px hsla(0,0%,72%,.5);outline:none
}
.veui-searchbox-action-button.veui-button{vertical-align:top;display:none;outline:none
}
.veui-searchbox.veui-disabled,.veui-searchbox.veui-readonly{background-color:#f7f7f7;border-color:#dbdbdb;color:#b8b8b8
}
.veui-searchbox.veui-disabled .veui-input-after,.veui-searchbox.veui-readonly .veui-input-after{overflow:hidden
}
.veui-searchbox.veui-disabled .veui-searchbox-action,.veui-searchbox.veui-disabled .veui-searchbox-action-icon,.veui-searchbox.veui-readonly .veui-searchbox-action,.veui-searchbox.veui-readonly .veui-searchbox-action-icon{cursor:not-allowed;color:#999
}
.veui-searchbox:not(.veui-disabled):not(.veui-readonly) .veui-searchbox-action:hover{color:#333
}
.veui-searchbox-suggestion-overlay{min-width:160px;max-height:280px;overflow-y:auto;background-color:#fff
}
.veui-searchbox-suggestion-overlay[ui~=large] .veui-searchbox-suggestion-item{font-size:16px
}
.veui-searchbox-suggestion-overlay[ui~=small] .veui-searchbox-suggestion-item{font-size:12px
}
.veui-searchbox[ui~=large] .veui-searchbox-suggestion-item{height:42px;font-size:16px
}
.veui-searchbox[ui~=small] .veui-searchbox-suggestion-item{height:32px;font-size:12px
}
.veui-searchbox[ui~=primary] .veui-searchbox-action .veui-searchbox-action-button{display:block
}
.veui-searchbox[ui~=primary] .veui-searchbox-action .veui-searchbox-action-icon{display:none
}


