
.veui-checkbox{position:relative;display:inline-block;color:#666;font-size:14px;line-height:16px;cursor:pointer
}
.veui-checkbox input{position:absolute;opacity:0
}
.veui-checkbox-box,.veui-checkbox-label{vertical-align:top;display:inline-block;transition-property:color;transition-duration:.2s
}
.veui-field>.veui-checkbox{line-height:36px
}
.veui-field>.veui-checkbox .veui-checkbox-box{vertical-align:text-top
}
.veui-checkbox .focus-visible~.veui-checkbox-box,.veui-checkbox .focus-visible~.veui-checkbox-label,.veui-checkbox:hover{color:#333
}
.veui-checkbox .focus-visible~.veui-checkbox-box{box-shadow:0 0 0 2px hsla(0,0%,72%,.5);outline:none
}
.veui-checkbox-box{position:relative;height:16px;width:16px;margin-right:8px;border:1px solid #666;border-radius:1px;background-color:#fff;font-size:16px;transition-property:box-shadow;transition-duration:.2s
}
.veui-checkbox-box .veui-icon{color:#333;position:absolute;top:50%;right:50%;bottom:50%;left:50%;-ms-transform:translate(-50%,-50%) scale(.8);transform:translate(-50%,-50%) scale(.8);transition-property:transform,opacity;transition-duration:.2s
}
.veui-checkbox-icon-enter,.veui-checkbox-icon-leave-to{opacity:0;-ms-transform:translate(-50%,-50%) scale(.6);transform:translate(-50%,-50%) scale(.6)
}
.veui-checkbox :checked+.veui-checkbox-box,.veui-checkbox:hover .veui-checkbox-box,.veui-checkbox :indeterminate+.veui-checkbox-box{border-color:#333
}
.veui-checkbox.veui-disabled{cursor:not-allowed
}
.veui-checkbox.veui-disabled .veui-checkbox-label{color:#999
}
.veui-checkbox.veui-disabled .veui-checkbox-box{border-color:#dbdbdb;background-color:#eee
}
.veui-checkbox.veui-disabled .veui-checkbox-box .veui-icon{color:#dbdbdb
}
.veui-checkbox[ui~=small]{font-size:12px;line-height:12px
}
.veui-field[ui~=small]>.veui-checkbox[ui~=small]{line-height:32px
}
.veui-field[ui~=small]>.veui-checkbox[ui~=small] .veui-checkbox-box{vertical-align:-2px
}
.veui-checkbox[ui~=small] .veui-checkbox-box{margin-right:6px;height:12px;width:12px;font-size:12px
}


