
.veui-slider{width:100%;min-width:210px;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;overflow:visible;outline:none
}
.veui-slider,.veui-slider-track-default-wrapper{position:relative
}
.veui-slider-track-default-bg{background-color:#dbdbdb
}
.veui-slider-track-default-fg,.veui-slider-track-default-sp{position:absolute;top:0
}
.veui-slider-track-default-sp{background-color:#c4e0fe
}
.veui-slider-track-default-fg{background-color:#3998fc
}
.veui-slider-track-default-marks{position:absolute;top:0;right:0;bottom:0;left:0;-ms-transform:translateY(50%);transform:translateY(50%)
}
.veui-slider-track-default-mark{border-left:1px solid #dbdbdb;position:absolute;-ms-transform:translateY(-50%);transform:translateY(-50%)
}
.veui-slider-thumb{position:absolute;left:0;background:none;padding:0;border:none;overflow:visible;-ms-transform:translateX(-50%);transform:translateX(-50%);cursor:pointer;transition-property:border-color,background-color,color,opacity,box-shadow;transition-duration:.2s
}
.veui-slider-thumb:focus{outline:none
}
.veui-slider-thumb.focus-visible .veui-slider-thumb-default{border-color:#2e7aca;box-shadow:0 0 0 2px rgba(116,183,253,.5);outline:none
}
.veui-readonly .veui-slider-thumb.focus-visible .veui-slider-thumb-default{box-shadow:0 0 0 2px hsla(0,0%,72%,.5);outline:none
}
.veui-slider-thumb-default{border:2px solid #3998fc;border-radius:100%;background-color:#fff
}
.veui-slider .veui-slider-track-default{height:36px;padding-top:16px
}
.veui-slider .veui-slider-track-default-progress{height:4px;border-radius:2px
}
.veui-slider .veui-slider-track-default-mark{height:10px
}
.veui-slider .veui-slider-thumb{top:11px
}
.veui-slider .veui-slider-thumb-default{width:14px;height:14px
}
.veui-slider[ui~=large] .veui-slider-track-default{height:42px;padding-top:19px
}
.veui-slider[ui~=large] .veui-slider-track-default-progress{height:4px;border-radius:2px
}
.veui-slider[ui~=large] .veui-slider-track-default-mark{height:10px
}
.veui-slider[ui~=large] .veui-slider-thumb{top:14px
}
.veui-slider[ui~=large] .veui-slider-thumb-default{width:14px;height:14px
}
.veui-slider[ui~=small] .veui-slider-track-default{height:32px;padding-top:14.5px
}
.veui-slider[ui~=small] .veui-slider-track-default-progress{height:3px;border-radius:1.5px
}
.veui-slider[ui~=small] .veui-slider-track-default-mark{height:8px
}
.veui-slider[ui~=small] .veui-slider-thumb{top:10px
}
.veui-slider[ui~=small] .veui-slider-thumb-default{width:12px;height:12px
}
.veui-slider[ui~=tiny] .veui-slider-track-default{height:28px;padding-top:13px
}
.veui-slider[ui~=tiny] .veui-slider-track-default-progress{height:2px;border-radius:1px
}
.veui-slider[ui~=tiny] .veui-slider-track-default-mark{height:6px
}
.veui-slider[ui~=tiny] .veui-slider-thumb{top:9px
}
.veui-slider[ui~=tiny] .veui-slider-thumb-default{width:10px;height:10px
}
.veui-slider[ui~=micro] .veui-slider-track-default{height:22px;padding-top:10px
}
.veui-slider[ui~=micro] .veui-slider-track-default-progress{height:2px;border-radius:1px
}
.veui-slider[ui~=micro] .veui-slider-track-default-mark{height:6px
}
.veui-slider[ui~=micro] .veui-slider-thumb{top:7px
}
.veui-slider[ui~=micro] .veui-slider-thumb-default{width:8px;height:8px
}
.veui-slider.veui-disabled .veui-slider-track-default-bg,.veui-slider.veui-readonly .veui-slider-track-default-bg{background-color:#e7e7e7
}
.veui-slider.veui-disabled .veui-slider-track-default-fg,.veui-slider.veui-readonly .veui-slider-track-default-fg{background-color:#b8b8b8
}
.veui-slider.veui-disabled .veui-slider-track-default-mark,.veui-slider.veui-readonly .veui-slider-track-default-mark{border-color:#e7e7e7
}
.veui-slider.veui-disabled .veui-slider-thumb-default,.veui-slider.veui-readonly .veui-slider-thumb-default{border-color:#b8b8b8
}


