
.veui-uploader{outline:none;width:400px;overflow:hidden
}
.veui-uploader-button-container{position:relative;margin-bottom:12px;overflow:hidden
}
.veui-uploader-button-container .veui-uploader-error,.veui-uploader-button-container .veui-uploader-tip{margin-top:8px
}
.veui-uploader-input-label{float:left;color:#333;cursor:pointer;margin:0 1em 8px 0
}
.veui-uploader-input-label-icon{margin-right:5px
}
.veui-uploader .veui-button.veui-uploader-input-label-disabled,.veui-uploader .veui-uploader-input-label-disabled{cursor:not-allowed;border-color:transparent;background-color:#eee;color:#999;box-shadow:none
}
.veui-uploader .veui-button.veui-uploader-input-label-disabled:hover,.veui-uploader .veui-uploader-input-label-disabled:hover{border-color:transparent;background-color:#eee;color:#999;box-shadow:none
}
.veui-uploader-input-label-image{position:relative;display:block;width:100%;height:100%;cursor:pointer;transition-property:background-color;transition-duration:.2s
}
.veui-uploader-input-label-image .veui-icon{font-size:50px;color:#666;position:absolute;top:auto;right:50%;bottom:50%;left:auto;-ms-transform:translate(50%,50%);transform:translate(50%,50%)
}
.veui-uploader-input-label-image:hover{background:#f7f7f7
}
.veui-uploader-input-label-image:active{background:#e7e7e7
}
.veui-uploader-list,.veui-uploader-list-image{padding:0;margin:0;list-style:none
}
.veui-uploader-list-image li,.veui-uploader-list li{margin:0 10px 5px 0;position:relative;transition-property:transform;transition-duration:.2s
}
.veui-uploader-list-image img,.veui-uploader-list img{width:auto;height:auto;max-width:100%;max-height:100%;position:absolute;top:auto;right:50%;bottom:50%;left:auto;-ms-transform:translate(50%,50%);transform:translate(50%,50%)
}
.veui-uploader-list-image-status,.veui-uploader-list-status{line-height:1.2
}
.veui-uploader-list-image-status>span,.veui-uploader-list-status>span{display:inline-block;vertical-align:middle;word-break:break-all
}
.veui-uploader-list-image-status:after,.veui-uploader-list-status:after{content:"";height:100%;display:inline-block;vertical-align:middle
}
.veui-uploader-list-image-success,.veui-uploader-list-success{position:absolute;top:-1px;right:-1px;bottom:-1px;left:-1px;padding:9px 4px;background-color:#fff;border:1px solid #dbdbdb;color:#5bc49f;cursor:pointer
}
.veui-uploader-list-container{position:relative;padding:3px 4px;color:#666;transition-property:background-color;transition-duration:.2s;text-align:right;overflow:hidden;white-space:nowrap
}
.veui-uploader-list-container:hover{background-color:#f7f7f7
}
.veui-uploader-list-container:hover .veui-uploader-button-remove{opacity:1
}
.veui-uploader-list-name{overflow:hidden;text-overflow:ellipsis;white-space:nowrap;line-height:22px;float:left;text-align:left;vertical-align:middle
}
.veui-uploader-list-status{display:inline-block;height:100%
}
.veui-uploader-list-success .veui-icon{position:relative;top:0
}
.veui-uploader-list-icon{font-size:12px;margin:5px 8px 0 0;float:left
}
.veui-uploader-list-retry{margin-right:6px;margin-left:10px
}
.veui-uploader-list-retry.veui-button[ui~=micro]{font-size:14px
}
.veui-uploader-list-retry .veui-icon{margin:4px 6px 0 0;font-size:12px
}
.veui-uploader-list .veui-uploader-progress{float:left;text-align:left;line-height:22px;margin-right:10px
}
.veui-uploader-list-image{float:left
}
.veui-uploader-list-image-container{width:140px;height:140px;padding:0;border:1px solid #dbdbdb;text-align:center;position:relative
}
.veui-uploader-list-image-container:after,.veui-uploader-list-image-container:before{display:table;content:""
}
.veui-uploader-list-image-container:after{clear:both
}
.veui-uploader-list-image-container label.veui-button{cursor:pointer;position:absolute;top:auto;right:50%;bottom:50%;left:auto;-ms-transform:translate(50%,50%);transform:translate(50%,50%);width:88px;height:28px;line-height:26px;padding:0
}
.veui-uploader-list-image-container:hover .veui-uploader-list-image-mask{opacity:1
}
.veui-uploader-list-image-mask{opacity:0;transition-property:opacity;transition-duration:.2s;position:absolute;top:0;right:0;bottom:0;left:0;background-color:rgba(0,0,0,.1)
}
.veui-uploader-list-image-mask-failure{background-color:none
}
.veui-button.veui-uploader-list-image-mask-remove{position:absolute;top:-1px;right:-1px
}
.veui-uploader-list-image-mask .veui-button.veui-uploader-input-label-disabled,.veui-uploader-list-image-mask .veui-button.veui-uploader-input-label-disabled:hover{background:none
}
.veui-uploader-list-image-status{margin-top:10%;height:40%
}
.veui-uploader-list-image-status>span{max-width:90%
}
.veui-uploader-list-image-success{padding:0;line-height:138px
}
.veui-uploader-list-image-success span{display:block
}
.veui-uploader-list-image-success .veui-icon{font-size:16px;vertical-align:text-bottom;margin-right:5px
}
.veui-uploader-list-image-mask-remove.veui-uploader-list-image-mask-remove-failure{position:absolute;display:none
}
.veui-uploader-list-image-container:hover .veui-uploader-list-image-mask-remove-failure{display:inline
}
.veui-uploader-list-image .veui-uploader-progress-bar,.veui-uploader-list-image .veui-uploader-progress-bar-full{left:calc(50% - 35px);max-width:70px
}
.veui-uploader-error{display:inline-block
}
.veui-uploader-error .veui-icon{vertical-align:-2px;margin-right:7px
}
.veui-uploader-tip{color:#999;display:inline-block
}
.veui-uploader-tip+.veui-uploader-error{display:block;clear:both;font-size:12px
}
.veui-uploader-error,.veui-uploader-failure{color:#ff5b5b;vertical-align:middle;cursor:help
}
.veui-uploader-success{color:#5bc49f;vertical-align:middle
}
.veui-uploader-failure,.veui-uploader-success{margin-left:8px
}
.veui-uploader-hide{display:none
}
.veui-uploader[ui~=horizontal]{width:100%
}
.veui-uploader[ui~=horizontal] .veui-uploader-list-image:after,.veui-uploader[ui~=horizontal] .veui-uploader-list-image:before,.veui-uploader[ui~=horizontal] .veui-uploader-list:after,.veui-uploader[ui~=horizontal] .veui-uploader-list:before{display:table;content:""
}
.veui-uploader[ui~=horizontal] .veui-uploader-list-image:after,.veui-uploader[ui~=horizontal] .veui-uploader-list:after{clear:both
}
.veui-uploader[ui~=horizontal] .veui-uploader-list-image li,.veui-uploader[ui~=horizontal] .veui-uploader-list li{float:left;margin-right:10px
}
.veui-uploader[ui~=horizontal] .veui-uploader-list-container{width:auto
}
.veui-uploader .veui-uploader-button-remove{margin-right:-1px;color:#666;opacity:0;vertical-align:middle;transition-property:opacity;transition-duration:.2s
}
.veui-uploader-list-enter,.veui-uploader-list-leave-to{opacity:0
}
.veui-uploader .veui-uploader-list-leave-active{position:absolute
}
.veui-uploader-fade-leave-active{opacity:0;transition-property:opacity;transition-duration:.2s
}
.veui-uploader-progress{display:block;position:relative;line-height:20px
}
.veui-uploader-progress-bar,.veui-uploader-progress-bar-full{position:absolute;height:4px;top:calc(50% - 2px);width:100%
}
.veui-uploader-progress-bar{background-color:#666
}
.veui-uploader-progress-bar-full{z-index:-1;background-color:#dbdbdb
}


