
.veui-dialog{display:none
}
.veui-dialog-box{position:fixed;top:0;right:0;bottom:0;left:0
}
.veui-dialog-box:not(.veui-dialog-box-mask){pointer-events:none
}
.veui-dialog-draggable{cursor:all-scroll
}
.veui-dialog-content{top:auto;right:50%;bottom:50%;left:auto;-ms-transform:translate(50%,50%);transform:translate(50%,50%);box-shadow:0 1px 4px rgba(0,0,0,.2);background:#fff;position:absolute;width:400px;max-height:calc(100vh - 40px);overflow-y:auto;outline:none;pointer-events:auto;transition-property:transform;transition-duration:.2s;-ms-transform-origin:50% 50%;transform-origin:50% 50%
}
[ui~=small] .veui-dialog-content{width:350px
}
[ui~=large] .veui-dialog-content{width:540px
}
[ui~=auto] .veui-dialog-content{width:auto
}
.veui-dialog-box[ui~=high] .veui-dialog-content{position:absolute;top:auto;right:50%;bottom:61.8%;left:auto;-ms-transform:translate(50%,61.8%);transform:translate(50%,61.8%)
}
.veui-dialog-content-foot,.veui-dialog-content-head{padding:0 20px
}
.veui-dialog-content-head{height:56px;line-height:56px
}
.veui-dialog-content-head-close,.veui-dialog-content-head-close:hover,.veui-dialog-content-head-title{color:#333
}
.veui-dialog-content-head-title{margin:0;font-weight:400;font-size:16px
}
.veui-dialog-content-head-close{padding:0;outline:none;transition-property:border-color,background-color,color,opacity,box-shadow;transition-duration:.2s
}
.veui-dialog-content-head-close,.veui-dialog-content-head-close:hover{border:none;background:none;float:right;height:14px;line-height:14px;text-align:center;font-size:14px;cursor:pointer;position:absolute;top:10px;right:10px
}
.veui-dialog-content-head-close:focus,.veui-dialog-content-head-close:hover{color:#3998fc
}
.veui-dialog-content-head-close.focus-visible{box-shadow:0 0 0 2px hsla(0,0%,72%,.5);outline:none
}
.veui-dialog-content-body{padding-top:0;margin:20px 20px 0
}
.veui-dialog-content-head+.veui-dialog-content-body{margin-top:10px
}
.veui-dialog-content-foot{padding:30px 20px 20px;text-align:center
}
.veui-dialog-content-foot .veui-button{min-width:94px;margin-right:20px
}
.veui-dialog-content-foot .veui-button:last-child{margin-right:0
}
.veui-dialog-box-mask{background-color:rgba(51,51,51,.5);overflow:auto
}
.veui-dialog-box-mask,.veui-dialog-box-mask[ui~=high]{position:fixed;top:0;right:0;bottom:0;left:0;-ms-transform:none;transform:none
}
.veui-overlay-enter .veui-dialog-content,.veui-overlay-leave-to .veui-dialog-content{-ms-transform:translate(50%,calc(50% - 10px));transform:translate(50%,calc(50% - 10px))
}
.veui-overlay-enter[ui~=high] .veui-dialog-content,.veui-overlay-leave-to[ui~=high] .veui-dialog-content{-ms-transform:translate(50%,calc(61.8% - 10px));transform:translate(50%,calc(61.8% - 10px))
}


