
.veui-alert{position:relative;padding:10px 20px;color:#666;display:table;width:100%;line-height:1.5
}
.veui-alert-close,.veui-alert-message,.veui-alert-nav,.veui-alert-state{display:table-cell
}
.veui-alert-state{padding-right:36px;vertical-align:middle
}
.veui-alert-icon{display:block;font-size:16px
}
.veui-alert-success{background-color:#eff9f5
}
.veui-alert-success .veui-alert-icon{color:#5bc49f
}
.veui-alert-warning{background-color:#fff5e6
}
.veui-alert-warning .veui-alert-icon{color:#fe9700
}
.veui-alert-info{background-color:#ebf5ff
}
.veui-alert-info .veui-alert-icon{color:#3998fc
}
.veui-alert-error{background-color:#ffefef
}
.veui-alert-error .veui-alert-icon{color:#ff5b5b
}
.veui-alert-message{width:100%;height:100%
}
.veui-alert-nav-indicator{margin-right:8px;margin-left:8px
}
.veui-alert-nav-indicator,.veui-alert-nav .veui-button{vertical-align:top;height:1.5em
}
.veui-alert-close,.veui-alert-nav{padding-left:20px;vertical-align:middle;white-space:nowrap
}
.veui-alert-close .veui-icon,.veui-alert-nav .veui-icon{font-size:12px
}
.veui-alert-close .veui-button,.veui-alert-nav .veui-button{height:auto;line-height:1.5
}
.veui-alert-close .veui-button .veui-icon,.veui-alert-nav .veui-button .veui-icon{margin-top:.375em
}
.veui-alert-close .veui-button.focus-visible,.veui-alert-close .veui-button:active,.veui-alert-close .veui-button:hover,.veui-alert-nav .veui-button.focus-visible,.veui-alert-nav .veui-button:active,.veui-alert-nav .veui-button:hover{color:#333
}
.veui-alert-close .veui-button.focus-visible,.veui-alert-nav .veui-button.focus-visible{box-shadow:0 0 0 2px hsla(0,0%,60%,.5);outline:none
}
.veui-alert-close .veui-button{float:left
}
.veui-alert-close-text{color:#3998fc
}
.veui-alert-close-text.focus-visible,.veui-alert-close-text:hover{color:#3389e3
}
.veui-alert-close-text:active{color:#2e7aca
}


