
.veui-tree,.veui-tree-item-group{list-style:none;padding:0;margin:0
}
.veui-tree-item{height:36px;line-height:36px;outline:none
}
.veui-tree-item:after,.veui-tree-item:before{display:table;content:""
}
.veui-tree-item:after{clear:both
}
.veui-tree-item.focus-visible{box-shadow:0 0 0 2px hsla(0,0%,72%,.5);outline:none
}
.veui-tree-item-expand-switcher,.veui-tree-item-label{float:left
}
.veui-tree-item-expand-switcher{height:14px;line-height:14px;display:inline-block;background:none;border:none;padding:0;vertical-align:top;margin-top:calc(18px - .5em);margin-right:4px;cursor:pointer;outline:none;transition-property:box-shadow,transform;transition-duration:.2s
}
.veui-tree-item-expand-switcher.focus-visible{box-shadow:0 0 0 2px hsla(0,0%,72%,.5);outline:none
}
.veui-tree-item-expand-switcher .veui-icon{width:14px;height:14px
}
.veui-tree-item-expanded .veui-tree-item-expand-switcher{-ms-transform:rotate(90deg);transform:rotate(90deg)
}
.veui-tree-item-group{display:none;padding-left:18px
}
.veui-tree-item-expanded+.veui-tree-item-group{display:block
}
.veui-tree-item-label:first-child{margin-left:18px
}
.veui-tree-item-clickable{cursor:pointer
}


